.screen-main {
  &__header {
    display: block;
    margin-bottom: 50px;
    color: #fff;
    text-align: center;
  }

  &__title {
    font-family: 'Eczar', sans-serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 38px;
  }

  &__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
}